import React from 'react'
import { useTransition, useSpring, animated } from 'react-spring'
import { useState, useEffect, useRef } from 'react'

const Tooltip = ({ children, id, origin, component, margin, off, always, animate }) => {

  const [tipOffset, setTipOffset] = useState(0)
  const far = Number(margin)
  const sideOffset = 6

  const [visible, setVisible] = useState(always)

  const wrapper = useRef()
  const tooltip = useRef()
  const svg = useRef()


  const move = useSpring({ from: { opacity: 0, scale: .85 }, opacity: 1, scale: 1, config: { tension: 1200, friction: 40, precision: 0.01 } })

  const transitions = useTransition(visible, {
    from: { opacity: 0, scale: .85 },
    enter: { opacity: 1, scale: 1 },
    leave: { opacity: 0, scale: .85 },
    config: { tension: 1200, friction: 40, precision: 0.01 }
  })

  const [position, setPosition] = useState({})
  const [offset, setOffset] = useState({})

  useEffect(() => {

    if (visible) {
      //Position of wrapper
      const w = Number(wrapper.current.offsetWidth)
      const h = Number(wrapper.current.offsetHeight)
      const l = Number(wrapper.current.offsetLeft)
      const t = Number(wrapper.current.offsetTop)

      //Dimension of tooltip
      const width = Number(tooltip.current.offsetWidth)
      const height = Number(tooltip.current.offsetHeight)

      //Svg
      const tipWidth = Number(svg.current.attributes.width.nodeValue)
      const tipHeight = Number(svg.current.attributes.height.nodeValue)

      switch (origin) {
        default:
        case "bottom":
        case "bottom-left":
        case "bottom-right":
        case "top":
        case "top-left":
        case "top-right":
          setTipOffset(Number((off / 100) * (width - tipWidth)))
          break;
        case "left-top":
        case "left":
        case "left-bottom":
        case "right-top":
        case "right":
        case "right-bottom":
          setTipOffset(Number((off / 100) * (height - tipWidth)))
          break;
      }

      switch (origin) {
        default:
        case "bottom":
          setPosition({ left: l + (w / 2) - width / 2, top: t + h + far, transformOrigin: "top center" })
          setOffset({ marginLeft: 0 })
          break
        case "bottom-left":
          setPosition({ left: (l + (w / 2) - width / 2) + (width / 2 - tipOffset - tipWidth / 2), top: t + h + far, transformOrigin: "top left" })
          setOffset({ marginLeft: tipOffset })
          break
        case "bottom-right":
          setPosition({ left: (l + (w / 2) - width / 2) - (width / 2 - tipOffset - tipWidth / 2), top: t + h + far, transformOrigin: "top right" })
          setOffset({ marginRight: tipOffset })
          break
        case "top":
          setPosition({ left: l + (w / 2) - width / 2, top: t - height - far, transformOrigin: "bottom" })
          setOffset({ marginLeft: 0 })
          break
        case "top-left":
          setPosition({ left: (l + (w / 2) - width / 2) + (width / 2 - tipOffset - tipWidth / 2), top: (t - height - far), transformOrigin: "bottom left" })
          setOffset({ marginLeft: tipOffset })
          break
        case "top-right":
          setPosition({ left: (l + (w / 2) - width / 2) - (width / 2 - tipOffset - tipWidth / 2), top: t - height - far, transformOrigin: "bottom right" })
          setOffset({ marginRight: tipOffset })
          break
        case "left":
          setPosition({ left: l - width + sideOffset - far, top: t + (h / 2) - (height / 2), transformOrigin: "right" })
          setOffset({ marginLeft: 0 })
          break
        case "left-top":
          setPosition({ left: l - width + sideOffset - far, top: t + (h / 2) - (tipOffset + tipHeight / 2), transformOrigin: "top right" })
          setOffset({ marginTop: tipOffset })
          break
        case "left-bottom":
          setPosition({ left: l - width + sideOffset - far, top: t + (h / 2) - height + tipOffset + tipHeight / 2, transformOrigin: "bottom right" })
          setOffset({ marginBottom: tipOffset })
          break
        case "right":
          setPosition({ left: l + w - sideOffset + far, top: t + (h / 2) - (height / 2), transformOrigin: "left" })
          setOffset({ marginLeft: 0 })
          break
        case "right-top":
          setPosition({ left: l + w - sideOffset + far, top: t + (h / 2) - (tipOffset + tipHeight / 2), transformOrigin: "top left" })
          setOffset({ marginTop: tipOffset })
          break
        case "right-bottom":
          setPosition({ left: l + w - sideOffset + far, top: t + (h / 2) - height + tipOffset + tipHeight / 2, transformOrigin: "bottom left" })
          setOffset({ marginBottom: tipOffset })
      }
    }
  }, [tipOffset, far, margin, off, origin, visible, always])

  return (
    <div key={id}>
      <div onMouseOver={() => setVisible(true)} onMouseLeave={() => !always ? setVisible(false) : setVisible(true)} ref={wrapper} className="content-wrapper">
        {children}
        {transitions((style, visible) => visible
          ? <animated.div onMouseOver={() => setVisible(true)} className={`coral tooltip ${origin}`} ref={tooltip} style={always ? { ...move, ...position } : { ...style, ...position }}>
            <div className="tip" style={offset}>
              <svg ref={svg} height="8" width="12">
                <path fillRule="evenodd" clipRule="evenodd" d="M5.7 1.06667C6.1 0.533333 6.9 0.533333 7.3 1.06667L12.5 8L0.5 8L5.7 1.06667Z" />
              </svg>
            </div>
            <div className="body">
              {component}
            </div>
          </animated.div>
          : '')}
      </div>
    </div>
  )
}

Tooltip.defaultProps = {
  always: false,
  origin: "bottom",
  margin: 8,
  off: 24,
  component: "Default tooltip description"
}

export default Tooltip
